.infinite-scroll {
    /* overflow: hidden;*/
    /* white-space: nowrap;*/
    /* background-color: #f4f4f4; Adjust background color */
    padding: 10px 0;
    font-size: 1.5em;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .scroll-content {
    display: inline-flex;
    align-items: center;
    animation: scroll 20s linear infinite;
  }
  
  .scroll-item {
    padding: 0 20px;
  }
  
  @keyframes scroll {
    0% {
      transform: translateX(100%);
    }
    100% {
      transform: translateX(-100%);
    }
  }
  